.sidebaroption{
    display: -ms-flexbox;
    display: flex;
    padding-left: 2px;
    cursor: pointer;
    font-size: 1.2rem;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.sidebaroption > h3{
    font-weight: 500;
}

.sidebaroption:hover{
    background-color: #340e36;
    opacity: 0.9;
}

.sidebaroption__icon{
    font-size: 3.5rem !important;
    padding: 10px;
}

.sidebaroption__hash{
    padding: 10px;
}
.sidebaroption__channel{
    padding: 10px 0;
}


