.header{
    background-color: var(--slack-color);
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    color: white;
    padding: 5px 2rem;
}

.header__left > .MuiSvgIcon-root{
    margin-left: auto;
    margin-right: 3rem;
}

.header__left{
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 0.3 1;
        flex: 0.3 1;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 2rem;
    
}


.header__center{
    -ms-flex: 0.4 1;
        flex: 0.4 1;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    background-color: #421f44;
    padding: 0 5rem;
    border-radius: 6px;
    border: 1px solid grey;
    color: grey;
}

.header__center > input{
    border: none;
    border-bottom: 1px solid transparent;
    background-color: transparent;
    text-align: center;
    min-width: 35vw;
    padding: 2px;
    color: darkgrey;
}

.header__center > input:hover{
    background-color: var(--slack-color);
}

.header__center > input:focus{
    outline: none;
    border-bottom: 1px solid var(--slack-color);
}

.header__right{
    -ms-flex: 0.3 1;
        flex: 0.3 1;
    -ms-flex-pack: end;
        justify-content: flex-end;
    display: -ms-flexbox;
    display: flex;
}


.header__right > .MuiSvgIcon-root{
    margin-left: auto;
}


